.rental-checkout{width:350px;background:lightblue;margin:0 auto;border-radius:5px;padding:0.75rem}.rental-checkout__form .input-fields{display:grid;grid-template-rows:repeat(5, 1fr);grid-template-columns:repeat(2, 1fr);row-gap:5px;column-gap:10px;margin-bottom:10px}.rental-checkout__form--submit{width:100%;margin-top:10px;border:4px solid white;background-color:lightgreen;font-weight:bolder;color:#464646;padding:1rem;font-size:18px;letter-spacing:2px;cursor:pointer}.rental-checkout__validation-error{background-color:#f85959;color:white;border:1px solid red;width:100%;text-align:center;font-size:20px;padding:1rem;position:fixed;top:0;left:0}
