.time-picker{margin-top:1rem;grid-template-rows:repeat(8, 1fr);grid-template-columns:1fr}.time-picker__hour-block{width:100%;margin-top:5px;background-color:white;z-index:0}.time-picker__hour-fraction--with-offset{width:100%;background-color:white;border:none;text-align:left;border:0 solid lightgrey;padding:0 5px;cursor:pointer}.time-picker__hour-fraction--with-offset{border-bottom-width:1px;font-size:16px;padding:5px}.time-picker__hour-fraction:not(:first-child){cursor:default}.time-picker__hour-fraction:not(:first-child) .hour-fraction__text{visibility:hidden}.time-picker__hour-fraction--available{background-color:lightgreen}.time-picker__hour-fraction--selected{background-color:darkgreen}
